<template>
  <v-app id="inspire">
    <v-card color="blue-grey darken-3" class="white--text">
      <v-card-title primary-title class="justify-center" >
        <div>
            <div class="headline text-xs-center hinter">Введите коэффициенты: </div>
            <div class="headline text-xs-center hinter"><small> <span class="variable">a</span>*x^2+<span class="variable">b</span>*x+<span class="variable">c </span>=0</small></div>
            <div class="headline text-xs-center equation"><code>{{equation}}</code></div>
        </div>
      </v-card-title>
      <v-card-actions>
          <v-text-field
                  v-model="a"
                  autofocus
                  type="number"
                  label="a"
                  :class ="{nulls: isZero(a)}"
                  dark
          ></v-text-field>
          <v-text-field
                  v-model="b"
                  type="number"
                  label="b"
                  :class ="{nulls: isZero(b)}"
                  dark
          ></v-text-field>
          <v-text-field
                  v-model="c"
                  type="number"
                  label="c"
                  dark
                  :class ="{nulls: isZero(c)}"
          ></v-text-field>
        <v-btn flat dark   @click="request">Решить</v-btn>
      </v-card-actions>
        <v-expand-transition>
        <v-card-text v-if="result">
            <div v-if="result" class="headline text-xs-center"><span class="variable">x</span>={{result.solve}}</div>
            <div v-if="result.fraction" class="headline text-xs-center"><small>{{result.fraction}}</small></div>
        </v-card-text>
        </v-expand-transition>
    </v-card>
      <v-snackbar
              v-model="isErr"
              color="error"
              multi-line
              :timeout="timeout"
              vertical
      >
          {{axiosErr}} <br>{{msgErr}}
          <v-btn
                  dark
                  flat
                  @click="isErr = false"
          >
              Close
          </v-btn>
      </v-snackbar>


  </v-app>
</template>

<script>
    import axios from 'axios'
    import qs from 'qs'
    import HelloWorld from './components/HelloWorld'

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  data () {
    return {
        a: 1,
        b: -2,
        c: -3,
        result: null,
        msgErr: null,
        axiosErr: null,
        isErr: false,
        timeout: 1700
      //
    }
  },
    methods: {
      isZero: function (p){
          return p === '0'
      },
        request: function () {
          this.result = null;
            axios.post('/equation',qs.stringify({a: this.a, b: this.b, c: this.c}))
                .then( (result) => {
                    this.isErr = false;

                    if (result.data.infinity) {
                        this.result = result.data;
                        this.result.solve = ' Любое число';
                        return;
                    }
                    if (result.data.nan) {
                        this.result = result.data;
                        this.result.solve= ' Нет корней';
                        return;
                    }
                    this.result = result.data;
                })
                .catch((err) =>{
                    this.result = null;
                    this.msgErr = err.response.data;
                    this.axiosErr = err;
                    this.isErr = true;
                } );
        }
        },
  computed: {
        equation: function () {
            let eq='';
            let firstsign = true;
            if(this.a !== '0') {
                eq += this.a + 'x^2';
                firstsign = false
            }

            if(this.b !== '0') {
                this.b<0 ?
                    eq+= '-' + Math.abs(this.b) + 'x'
                    : eq+= (firstsign? '' : '+') + this.b + 'x';
                firstsign = false
            }
            if(this.c !== '0'){
                this.c<0 ?
                    eq+='-'+ Math.abs(this.c)  :
                    eq+= (firstsign? '' : '+')  + this.c;
            }


            eq += '= 0';
        return eq
        }
  }
}
</script>

<style scoped>
    .hinter {
        color: rgba(134, 165, 173, 0.93);
    }
    .equation {
     font-size: 300% !important;

    }
    .variable {
        color: rgba(208, 241, 249, 0.93);
        font-weight: bold;
    }
    .nulls {
        background: #4f353b;
    }
</style>